//  Lora
@font-face {
    font-family: 'Noto Serif JP';
    font-style: normal;
    font-weight: 400;
    font-display: block;
    src: url("../webfonts/googlefonts/NotoSerifJP/NotoSerifJP-Regular.woff2") format("woff2"),
    url("../webfonts/googlefonts/NotoSerifJP/NotoSerifJP-Regular.woff") format("woff"),
    url("../webfonts/googlefonts/NotoSerifJP/NotoSerifJP-Regular.otf") format("truetype");
    font-display: swap;
}

//  Lora
@font-face {
    font-family: 'Lora';
    font-style: normal;
    font-weight: 400;
    font-display: block;
    src: url("../webfonts/googlefonts/Lora/static/Lora-Medium.woff2") format("woff2"),
